	module.exports = {"data":{"site":{"siteMetadata":{"title":"DeepCold Blog","author":"DeepCold"}},"markdownRemark":{"id":"/Users/deepcold/iserver/deepcoldy.github.io/src/pages/older/2015-09-11.md absPath of file >>> MarkdownRemark","html":"<p>在手机测试中经常需要测试微信的使用场景，但是由于安卓版本的微信没有提供清理缓存的功能，所以需要在网页引用链接时就考虑到清缓存的问题。</p>\n<blockquote>\n<p>IPhone没有此问题</p>\n</blockquote>\n<p>在我看来最理想的解决方案是，在引用链接时缀一个后缀。</p>\n<p>比如&#x3C;img src=“header.jpg?&#x3C;?php echo time();?>”></p>\n<p>或者&#x3C;img src=“header.jpg?rand=&#x3C;?php echo $rand; ?>”></p>\n<p>就这样。</p>","frontmatter":{"title":"微信安卓浏览器有缓存的问题","date":"September 11, 2015"}}},"pathContext":{"slug":"/older/2015-09-11/","previous":{"fields":{"slug":"/older/2017-09-11/"},"frontmatter":{"title":"Sass几大功能总结"}},"next":{"fields":{"slug":"/older/2015-09-12/"},"frontmatter":{"title":"随便写个轮播的滚动效果"}}}}

